<template>
    <div class="shopWarp">
        <nav>
            <i class="el-icon-arrow-left" @click="zuoBtn"></i>
        </nav>
        <header>
            <div class="header_div">
                <img src="" alt="">
            </div>
            <section class="header_section">
                <router-link to="/shopDetail">
                    <section class="header_section2">
                        <img :src="'https://elm.cangdu.org/img/' + shopArr.image_path"  alt="">
                    </section>
                    <section class="header_right">
                        <h4>{{shopArr.name}}</h4>
                        <p class="right_p1">商家配送/分钟到达/配送费￥{{shopArr.float_delivery_fee}}</p>
                        <p class="right_p2 ellipsis">公告：{{shopArr.promotion_info}}</p>
                    </section>
                    <i class="el-icon-arrow-right header_i" style="color: #fff"></i>
                </router-link>
                <footer class="header_footer">
