                    </p>
                    <p class="footer_p2">{{activeArr.length}}个活动</p>
                    <i class="el-icon-arrow-right footer_i"></i>
                </footer>
            </section>
        </header>
        <section class="change_show_type">
            <div>
                <span :class="{activity_show1:is_activity_show1}" @click="clickBtn">商品</span>
            </div>
            <div>
                <span :class="{activity_show1:is_activity_show2}" @click="pingBtn">评价</span>
            </div>
        </section>
        <section class="count"  v-if="shangShow">
            <section class="count_shangpin">
                <section class="count_left">
                    <ul>
                        <li :class=" index == dianji ? 'count_left_li':'count_left_li_1'" v-for="(s ,index) in shangArr" :key="s.id" @click="fenBtn(index,s.name,s.description)">
                            <el-badge :value="itemCount(s.id)" :hidden="itemCount(s.id) ? false : true">
                                <span>{{s.name}}</span>
                            </el-badge>
                            <!--{{s.name}}-->
                        </li>
                        <!--<el-badge :value="itemCount(s.id)" :hidden="itemCount(s.id) ? false : true">-->
                            <!--<span>{{s.name}}</span>-->
                        <!--</el-badge>-->
                    </ul>
                </section>
                <section class="count_right">
                    <!--<ul>-->
                    <ul v-for="(s ,index) in shangArr" :key="index">
                        <li class="count_right_li">
                            <section class="right_section1">
                                <section class="right_section2">
                                    <!--<strong>{{leftName}}</strong>-->
                                    <!--<span>{{leftDescription}}</span>-->
                                    <strong>{{s.name}}</strong>
                                    <span>{{s.description}}</span>
                                </section>
                                <span class="dian" @click="dianShow = !dianShow">...</span>
                                <p v-if="dianShow">
                                    <span>{{leftName}}{{leftDescription}}</span>
                                </p>
                            </section>

                            <ul>
                                <li v-for="(right, index1) in s.foods" :key="index1">
                                    <!--<section class="right_list" v-for="(right, index1) in rightArr" :key="index1">-->
                                    <section class="right_list">
                                        <div class="right_list_div">
                                            <section class="right_list_div_img">
                                                <img :src="'https://elm.cangdu.org/img/'+right.image_path" alt="">
                                            </section>
                                            <section class="right_list_div_section">
                                                <h3>
                                                    <strong>{{right.name}}</strong>
                                                    <ul>
                                                        <li class="attribute_new" v-if="right.attributes.length ? true : false">
                                                            <p class="">新品</p>
                                                        </li>
                                                        <li class="attribute_zhao" v-if="right.attributes.length ? true : false">
                                                            <p>招牌</p>
                                                        </li>
                                                    </ul>
                                                    <!--<ul class=""></ul>-->
                                                </h3>
                                                <p>{{right.description}}</p>
                                                <p>
                                                    <span>月售{{right.month_sales}}份</span>
                                                    <span>好评率{{right.satisfy_rate}}%</span>
                                                </p>
                                                <p>
                                             <span>
                                                 {{fengNull(right.activity)}}
                                             </span>
                                                </p>
                                            </section>
                                        </div>
                                        <footer class="right_list_footer">
                                            <section class="footer_price">
                                                <span>￥</span>
                                                <span>{{right.specfoods[0].price}}</span>
                                                <span>起</span>
                                            </section>
                                            <section class="right_list_footer_guige">
                                                <!--<span  v-if="right.specifications.length ? true : false" @click="guiBtn(index1,right.specfoods,right.category_id)">选规格</span>-->
                                                <span  v-if="right.specifications.length ? true : false" @click="guiBtn(right.specfoods,right.category_id,right)">选规格</span>
                                            </section>
                                            <!-- + -->
                                            <section class="right_list_footer_jia" v-if="right.specifications.length ? false:true">
                                                <!--<span class="" v-if="isShowJian">-->
                                                    <!--<i class="el-icon-minus" @click="jianBtn(index1)" v-if="isShowJian"></i>-->
                                                <!--</span>-->
                                                <span  @click="right.specifications.length ? xuanjianBtn():jianBtn(foodNum(right),right.category_id, right.item_id, right.specfoods[0].food_id, right.specfoods[0].name, right.specfoods[0].price, right.specfoods[0].specs, right.specfoods[0].packing_fee, right.specfoods[0].sku_id, right.specfoods[0].stock)">
                                                    <i class="el-icon-minus"></i>
                                                </span>
                                                <span class="cart_num1">{{foodNum(right)}}</span>
                                                <!--<span v-if="isShowZero">{{right.__v}}</span>-->

                                                <span class="">
                                            <!--<i class="el-icon-plus" @click="jiaBtn(index1)"></i>-->
                                                    <i class="el-icon-plus"
                                                       @click="jiaBtn(right.category_id, right.item_id, right.specfoods[0].food_id, right.specfoods[0].name, right.specfoods[0].price, right.specfoods[0].specs, right.specfoods[0].packing_fee, right.specfoods[0].sku_id, right.specfoods[0].stock)"></i>
                                                </span>
                                            </section>
                                            <section class="right_list_footer_add" style="display: none">
                                        <span>
                                            <i class="el-icon-remove-outline"></i>
                                        </span>
                                                <span>2</span>
                                                <span>
                                            <i class="el-icon-circle-plus-outline"></i>
                                        </span>
                                            </section>
                                        </footer>
                                    </section>
                                </li>
                            </ul>

                        </li>
                    </ul>
                </section>
            </section>
            <section class="count_goumai">
                    <section class="gou_section1">
                        <div class="gou_div1" @click="gouwucheBtn" :style="allFoodsPrice?'background-color:#3190e8':''">
                            <!--<span class="gou_div1_span" v-if="isShowNum">{{countAll}}</span>-->
                            <span class="gou_div1_span" v-if="allFoodsPrice">{{allItemCount}}</span>
                            <i class="el-icon-shopping-cart-2" style="font-size: 1.2rem;color: #fff" ></i>
                        </div>
                        <div class="gou_div2">
                            <!--<div>￥{{priceAll}}.00</div>-->
                            <div>￥{{allFoodsPrice}}.00</div>
                            <div>配送费￥5</div>
                        </div>
                    </section>
                    <section class="gou_section2" :style="allFoodsPrice?'background-color:#4cd964':''" @click="xiadanBtn">
                        <!--<span>{{countAll ? '去下单':'还差¥20起送'}}</span>-->
                        <span>{{allFoodsPrice ? '去下单':'还差¥20起送'}}</span>
                    </section>
            </section>
            <section class="gou_wu_che" v-if="isShowGou">
                <header class="gouwu_header">
                    <span class="gouwu_header_span">购物车</span>
                    <div class="gouwu_header_div" @click="qingkongBtn">
                        <i class="el-icon-delete" style="font-size: .6rem"></i>
                        <span>清空</span>
                    </div>
                </header>
                <section class="cartFood">
                    <ul>
                        <li class="cart_food_li" v-for="(good,index) in carArr" :key="index">
                            <div class="cart_food_li_div1">{{good.name}}</div>
                            <div class="cart_food_li_div2">
                                <span>￥</span>
                                <span class="cart_food_li_div1_span">{{good.price}}</span>
                            </div>
                            <section class="cart_list_control">
                                <span class="cart_list_control_span1" @click="jianBtn1
                                (good.shopId,good.category_id, good.item_id, good.food_id, good.name, good.price, good.specs, good.packing_fee, good.sku_id, good.stock)">
                                    <i class="el-icon-remove-outline" style="font-size: .9rem;color: #3190e8;"></i>
                                </span>
                                <span class="cart_list_control_span2">{{good.num}}</span>
                                <span class="cart_list_control_span1"  @click="jiaBtn1
                                (good.shopId,good.category_id, good.item_id, good.food_id, good.name, good.price, good.specs, good.packing_fee, good.sku_id, good.stock)">
                                    <i class="el-icon-circle-plus-outline" style="font-size: .9rem;color: #3190e8;"></i>
                                </span>
                            </section>
                        </li>
                    </ul>
                </section>
            </section>
        </section>
        <!--评价-->
        <section class="count_pingjia" v-if="pingShow">
            <section class="">
                <div class="ping_header">
                    <div class="ping_header_left">
                        <p>{{fenArr.food_score.toFixed(1)}}</p>
                        <p>综合评价</p>
                        <p>高于周边商家{{fenArr.compare_rating.toFixed(2)*100}}%</p>
                    </div>
                    <div class="ping_header_right">
                        <p class="ping_header_right_p1">
                            <span>服务态度</span>
                            <span></span>
                            <span>{{fenArr.overall_score.toFixed(1)}}</span>
                        </p>
                        <p class="ping_header_right_p1">
                            <span>菜品评价</span>
                            <span></span>
                            <span>{{fenArr.service_score.toFixed(1)}}</span>
                        </p>
                        <p class="ping_header_right_p3">
                            <span>送达时间</span>
                            <span>分钟</span>
                        </p>
                    </div>
                </div>
                <!--评价内容-->
                <div class="ping_count">
                    <ul class="ping_count_ul">
                        <li v-for="(list, index) in fenleiArr" :key="list._id" @click="infoBtn(index)" :style="">
                            {{list.name}}（{{list.count}}
                        </li>
                    </ul>

                    <ul class="ping_info">
                        <li class="ping_info_li" v-for="info in infoArr" :key="info._id">
                            <img src="" alt="" class="ping_info_li_img">
                            <div class="li_div">
                                <section class="rating_list_details">
                                   <section class="username_star">
                                       <p class="username">{{infoArr.username}}</p>
                                       <p class="star_desc">
                                           <span class="rating_container"></span>
                                           <span class="time_spent_desc">{{infoArr.time_spent_desc}}</span>
                                       </p>
                                   </section>
                                    <time class="rated_at">{{infoArr.rated_at}}</time>

                                </section>
                                <ul class="food_img_ul">
                                    <li v-for="tu in imgArr" :key="tu._id">
                                        <img :src="'https://fuss10.elemecdn.com/' + tu.image_hash" alt="">
                                    </li>
                                </ul>
                                <ul class="food_img_ul">
                                    <li class="ellipsis" v-for="chi in chiArr" :key="chi._id">{{chi.food_name}}</li>
                                </ul>
                            </div>
                        </li>
                    </ul>
                </div>
            </section>

        </section>
        <section v-if="guiShow">
            <div class="specs_cover">
            </div>
            <div class="specs_list">
                <header class="specs_list_header">
                    <h4 class="ellipsis">{{guigeName}}</h4>
                    <i class="el-icon-close" @click="chaBtn"></i>
                </header>
                <section class="specs_details">
                    <h5 class="specs_details_title">规格</h5>
                    <ul >
                        <li :class="index == guigeArrBgc ? 'specs_activity' : 'specs_activity1 ' " v-for="(g, index) in guigeArr" :key="index" @click="guiLiBtn(index,g.specs_name,g)">{{g.specs[0].value}}</li>
                        <!--<li class="">a</li>-->
                    </ul>
                </section>
                <footer  class="specs_footer">
                    <div class="specs_price">
                        <span >¥ </span>
                        <span >{{guiLiPrice.price}}</span>
                    </div>
                    <div class="specs_addto_cart" @click="gouBtn(guigeId, guigeArr[guigeArrBgc].item_id, guigeArr[guigeArrBgc].food_id, guigeName, guiLiPrice.price, guigeArr[guigeArrBgc].specs[0].value,
  guigeArr[guigeArrBgc].packing_fee, guigeArr[guigeArrBgc].sku_id, guigeArr[guigeArrBgc].stock)">加入购物车</div>
                </footer>
            </div>
        </section>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        name: "Shop",
        data(){
            return {
                shopId:'',
               shopArr:[],//存储根据商铺id请求的商铺信息
                isShow:true,//判断是否有活动的显隐
                activeArr:[],//接收是否有活动
                leftName:'',
                leftDescription:'',
                first:true,//为了进来的时候就显示右边的数据
                icon_name:'',

                foodPro:[],//获取的商品的分类
                // name:'',
                description:'',
                shangArr:[],//左边分类数据
                rightArr:[],//右边
                dianShow:false,//三个点的弹窗
                rightPrice:[],//specfoods数组

                //点击商品  评价的切换
                shangShow:true,
                pingShow:false,
                is_activity_show1:true,
                is_activity_show2:false,

                //接收评分的数组
                fenArr:[],
                fenleiArr:[],//分类信息
                infoArr:[],//
                imgArr:[],
                chiArr:[],
                dianji:100,
                attributesArr:[],//看是招牌还是新店

                icon_name1:"招牌",
                icon_name2:"新",

                attributesShow:true,

                guiArr:[],//规则
                guiShow:false,
                guigeName:'',
                guigeId:'',//选规格的id
                guigeArr:[],//点击先规格拿到的规格里的内容
                guigeArrBgc:100,//判断规格点击变颜色
                guiLiPrice:'',//规格的价格
                guiLiName:'',//规格里面的名字


                //购物车的-号隐藏
                isShowJian:false,
                isShowZero:false,


                shopCount:0,
                priceAll:0,//价格
                countAll:0,//数量
                goodsIndex:[],

                //点击规格里的加入购物车
                gouShow:false,
                isShowNum:false,//购物车的数


                //下面购物车的显隐
                isShowGou:false,

                foosArr:[],//右边分类的

                carArr:[],
                shopCarts:null,

            }
        },
        created(){
            this.shopId = localStorage.getItem('shopId');
            this.axios.get("/shopping/restaurant/" + localStorage.getItem("shopId") + '').then(res=>{
                // console.log(res)
                this.shopArr = res;
                this.activeArr = res.activities;
                // console.log(this.activeArr);
                if(this.activeArr.length===0){
                    // this.name = ''
                    this.icon_name = '';
                    this.description='没优惠'
                }else{
                    // this.name = res.activities[0].name
                    this.icon_name = res.activities[0].icon_name
                    this.description=res.activities[0].description
                }
            }).catch(err=>{})
            //商品的
            this.axios.get("/shopping/v2/menu" ,{
                params:{
                    restaurant_id:localStorage.getItem("shopId")
                }
            }).then(res=>{
                // console.log(res);
                this.shangArr = res;
                // this.foosArr = res.foos;
                // console.log(shangArr.foods)
            }).catch(err=>{});

            //评价
            this.axios.get('/ugc/v2/restaurants/' + localStorage.getItem("shopId")+ '/ratings/scores').then(res=>{
                // console.log(res);
                this.fenArr = res;
            }).catch(err=>{})
            //评价信息
            this.axios.get("/ugc/v2/restaurants/"+ localStorage.getItem("shopId")+ "/ratings/tags").then(res=>{
                // console.log(res)
                this.fenleiArr = res;
            }).catch(err=>{})
            //评价内容
            this.axios.get("/ugc/v2/restaurants/"+ localStorage.getItem("shopId")+ "/ratings").then(res=>{
                // console.log(res)
                this.infoArr = res[0];
                this.imgArr = res[0].item_ratings;
                this.chiArr = res[0].item_ratings;
                // console.log(this.imgArr)
            }).catch(err=>{})
            //初始化vueX
            this.$store.commit("init_buy_cart");

            // 处理购物车数据
            this.handleCartData();
        },
        watch: {
            shopCart: {
                handler(newV, oldV) {
                    // console.log("shopCarts改变");
                    this.handleCartData();
                },
                deep: true
            }
        },
            methods: {
            zuoBtn(){
                this.$router.push({path:"/msite"});
            },
                handleCartData(){
                    this.carArr = []; //存储下面购物车里的数据
                    this.shopCarts = this.$store.state.cartList;
                    Object.keys(this.shopCarts).forEach(shopId=>{
                        Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                            Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                                Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                                    let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                                    // console.log(foodItem);
                                    let newFoodItem = {...foodItem, category_id, item_id, food_id,shopId}
                                    // this.carArr.push(newFoodItem);
                                    if (newFoodItem.shopId == this.shopId) {
                                        this.carArr.push(newFoodItem);
                                    }
                                    // console.log(this.carArr)
                                });
                            })
                        })
                    })
                },

                //商品
                clickBtn() {
                    this.shangShow = !this.shangShow
                    this.pingShow = !this.pingShow;
                    this.is_activity_show1 = !this.is_activity_show1;
                    this.is_activity_show2 = !this.is_activity_show2;
                },
                //评价
                pingBtn() {
                    this.shangShow = !this.shangShow
                    this.pingShow = !this.pingShow;
                    this.is_activity_show1 = !this.is_activity_show1;
                    this.is_activity_show2 = !this.is_activity_show2;
                },
                fenBtn(i, n, d) {
                    // this.dianji = i;
                    // console.log(s);
                    // console.log(i);
                    // this.rightArr = this.shangArr[i].foods;
                    // this.leftName = n;
                    // console.log(this.rightArr);
                    // this.leftDescription = d;
                    // console.log(this.rightArr)
                    // console.log(this.leftDescription);
                    // console.log(this.rightArr)

                    //规格的
                    // this.guiArr = this.rightArr[].specifications;
                    // console.log(this.guiArr);


                    this.dianji = i;
                    let selector = ".count_right>ul:nth-child(" + (i + 1) + ")>li";
                    this.$el.querySelector(selector).scrollIntoView({
                        behavior: "smooth",  // 平滑过渡
                        block: "start"  // 上边框与视窗顶部平齐。默认值
                    });

                },
                //判断是否是蜂鸟函数就是
                fengNull(i) {
                    // console.log(i)
                    if (i == null) {
                        return ''
                    } else {
                        return i.image_text
                    }
                },
                //判断是新品还是招牌
                // tese(i){
                //     console.log(i)
                //     if (i[0] == null){
                //         return ''
                //     }  else{
                //         return i.icon_name
                //     }
                // },

                //点击规格
                guiBtn(s, c,r) {
                    this.guiShow = true;
                    // console.log(r)
                    // this.guigeArr = this.rightArr[i].specfoods,
                    this.guigeArr = s
                    console.log(this.guigeArr);
                    // this.guigeName = this.rightArr[i].name;
                    this.guigeName = s[0].name
                    // console.log(this.guigeName)
                    this.guigeId = c;//左边分类的id  category_id
                    console.log(c)
                    // this.gouShow = true;
                },
                // guiBtn(i,cId){
                //     this.xuanguigeIsshow = !this.xuanguigeIsshow
                //     // console.log(i)
                //     this.xuanguigeArr = i;
                //     // console.log(this.xuanguigeArr);
                //     this.xuanguigeName = i[0].name;
                //     this.xuanguigecId = cId;
                //     // console.log(this.xuanguigeName)
                // },
                //点击规格里的购物车的时候
                gouBtn(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock) {
                    console.log(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock)
                    this.$store.commit("add_cart", {shopid: this.shopId, category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock});
                    this.guiShow = false;

                    // this.axios.get()
                },
                guiLiBtn(i, g_name, g) {
                    // console.log(i)
                    this.guigeArrBgc = i;//点击里面的index下标
                    // console.log(g)//里面的所有信息

                    // this.guiLiPrice = this.guigeArr[i];//里面的价格
                    this.guiLiPrice = this.guigeArr[i];
                    // console.log(g.price)

                    // console.log(g_name);//点击的name
                    this.guiLiName = g_name;
                    // console.log(this.guiLiName)
                },

                //点击×的时候
                chaBtn() {
                    this.guiShow = false;
                },
                //点击+ 号
                // jiaBtn(i) {
                //     // console.log(i)
                //     this.rightArr[i].__v++;
                //     this.priceAll += this.rightArr[i].specfoods[0].price;
                //     this.countAll++;
                //     if (this.rightArr[i].__v > 0) {
                //         this.isShowJian = true;
                //         this.isShowZero = true
                //         this.isShowNum = true;
                //     }
                //     var key = 0;
                //     for (var k = 0; k < this.goodsIndex.length; k++) {
                //         if (this.goodsIndex[k][0] == i && this.goodsIndex[k][1] == i) {
                //             key += 1;
                //         }
                //     }
                //     if (key == 0) {
                //         this.goodsIndex.push([i, i]);
                //     }
                // },

                jiaBtn(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock){

                    this.$store.commit("add_cart", {shopid: this.shopId, category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock});
                },

                //-号
                // jianBtn(i) {
                //     // console.log(i)
                //     this.rightArr[i].__v--;
