            computed:{
                allFoodsPrice() {
                    let allFoodsCount = 0;
                    if (this.shopCart) {
                        let allFoods = this.$store.state.cartList;
                        // Object.values(allFoods).forEach(shopFoods => {
                        //     Object.values(shopFoods).forEach(cateFoods => {
                        //         Object.values(cateFoods).forEach(itemFoods => {
                        //             Object.values(itemFoods).forEach(foods => {
                        //                 // console.log("+!!!!!!",foods);
                        //                 allFoodsCount += foods.num*foods.price;
                        //             })
                        //         })
                        //     })
                        // })
                        Object.keys(this.shopCarts).forEach(shopId => {
                            Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                                Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                                    Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                                        let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                                        let newFoodItem = {...foodItem, category_id, item_id, food_id, shopId}
                                        if (newFoodItem.shopId == this.shopId) {
                                            allFoodsCount += newFoodItem.num*newFoodItem.price;
                                            // console.log(allFoodsCount);
                                            localStorage.setItem("allPrice",allFoodsCount);
                                        }
                                    });
                                })
                            })
                        })
                    }
                    return allFoodsCount;
                },
                allItemCount(){
                    let allItemCount = 0;
                    if (this.shopCart) {
                        let allFoods = this.$store.state.cartList;
                        // Object.values(allFoods).forEach(shopFoods => {
                        //     Object.values(shopFoods).forEach(cateFoods => {
                        //         Object.values(cateFoods).forEach(itemFoods => {
                        //             Object.values(itemFoods).forEach(foods => {
                        //                 allItemCount += foods.num;
                        //             })
                        //         })
                        //     })
                        // })
                        Object.keys(this.shopCarts).forEach(shopId => {
                            Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                                Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                                    Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                                        let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                                        let newFoodItem = {...foodItem, category_id, item_id, food_id, shopId}
                                        if (newFoodItem.shopId == this.shopId) {
                                            allItemCount += newFoodItem.num;
                                        }
                                    });
                                })
                            })
                        })
                    }
                    return allItemCount;
                },
                itemCount() {
                    return cateid => {
                        let itemFoodCount = 0;
                        if (this.shopCart && this.shopCart[cateid]) {
                            console.log(this.shopCart[cateid]);
                            Object.values(this.shopCart[cateid]).forEach(foods => {
                                Object.values(foods).forEach(food => {
                                    // console.log(food);
                                    itemFoodCount += food.num;
                                })
                            })
                        }
                        return itemFoodCount;
                    }
                },
                ...mapState([
                    'cartList'
                ]),
                shopCart: function (){
                    return Object.assign({},this.$store.state.cartList[this.shopId]);
                },
                foodNum(){
                    return  foods=> {
                        let category_id = foods.category_id;
                        let item_id = foods.item_id;
                        let itemNum;
                        if (this.shopCart&&this.shopCart[category_id]&&this.shopCart[category_id][item_id]) {
                            let num = 0;
                            Object.values(this.shopCart[category_id][item_id]).forEach((item,index) => {
                                // console.log("++++!!!!!!!",item ? item.num : 0);
                                itemNum =  item ? item.num : 0;
                                num += itemNum;
                            })
                            return itemNum;
                        }else {
                            return 0;
                        }
                    }
                }

            }
    }
